.flex {
  display: flex;
  gap: 0.25rem;
  padding: 0.25rem;
  flex-wrap: wrap;
}
.grid {
  display: grid;
}
main {
  .functional.flex {
    color: black;
    font-size: 1.25rem;
    .item {
      flex: 1;

      min-width: 19.5rem;
      display: flex;
      span {
        padding: 0.5rem 0;
        flex: 1;
        text-align: center;
      }
      span:first-child {
        border-top-left-radius: 0.5rem;
        border-bottom-left-radius: 0.5rem;
      }
      span:last-child {
        border-top-right-radius: 0.5rem;
        border-bottom-right-radius: 0.5rem;
      }
    }
    .radio {
      background-color: var(--theme-color);
      border-radius: 0.5rem;
    }
    .select {
      select {
        border: none;
        width: 100%;
        background-color: var(--theme-color);
        border-radius: 0.5rem;
        font-size: 1.25rem;
        padding: 0.5rem;
        color: black;
        text-align: center;
      }
    }
  }
}

.add {
  /* width: 100%; */
  background-color: white;
  display: flex;
  justify-content: center;

  border-radius: 0.5rem;
}

#ResonatorsAdd {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 1rem 0rem;
  gap: 0.5rem;
  img {
    border-radius: 0.5rem;
    margin: auto;
    width: 100%;
  }
  div {
    width: 100%;
    border-radius: 0.5rem;
    padding: 0.25rem;
    background-color: var(--theme-color-alpha-100);
    flex-direction: column;
    gap: 0.25rem;
  }
  .name {
    text-align: center;
  }
}
