@import url(css/main.css);
@import url(css/footer.css);

:root {
  background-color: black;
  color: white;

  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);

  --theme-color: #dab67dff;
  --theme-color-alpha-700: #dab67dcc;
  --theme-color-alpha-500: #dab67d99;
  --theme-color-alpha-300: #dab67d66;
  --theme-color-alpha-100: #dab67d33;

  --element-응결: #41aefb;
  --element-용융: #f0744e;
  --element-전도: #b46bff;
  --element-기류: #55ffb5;
  --element-회절: #f8e56c;
  --element-인멸: #e649a6;

  --5-star: #f1dd60;
  --4-star: #a065e5;
  --3-star: #3badff;
  --2-star: #7bdd42;
  --1-star: #a7a7a7;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  word-break: keep-all;
  font-family: 'One Mobile', -apple-system, sans-serif;
  text-align: center;
}

div#root {
  scroll-snap-type: y mandatory;
  scroll-padding: 0.5rem;
  overflow-x: scroll;
  margin: auto;

  height: 100dvh;
}

div#root,
footer > nav {
  max-width: 20rem;
  margin: auto;
}

@media (min-width: 40rem) {
  div#root,
  footer > nav {
    max-width: 40rem;
  }
  section[data-section='list'] {
    grid-template-columns: repeat(2, 1fr);
  }
  #ResonatorsAdd {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media (min-width: 60rem) {
  div#root,
  footer > nav {
    max-width: 60rem;
  }
  section[data-section='list'] {
    grid-template-columns: repeat(3, 1fr);
  }
  #ResonatorsAdd {
    grid-template-columns: repeat(9, 1fr);
  }
}
@media (min-width: 80rem) {
  div#root,
  footer > nav {
    max-width: 80rem;
  }
  section[data-section='list'] {
    grid-template-columns: repeat(4, 1fr);
  }
  #ResonatorsAdd {
    grid-template-columns: repeat(12, 1fr);
  }
}
@media (min-width: 100rem) {
  div#root,
  footer > nav {
    max-width: 100rem;
  }
  section[data-section='list'] {
    grid-template-columns: repeat(5, 1fr);
  }
  #ResonatorsAdd {
    grid-template-columns: repeat(15, 1fr);
  }
}

input,
select {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  text-align-last: center;
}
