.detail {
  max-width: 60rem;
  margin: auto;
  padding: 0.5rem 0;
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;

  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(6, 1fr);

  @media (min-width: 40rem) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  @media (min-width: 60rem) {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  section {
    scroll-snap-align: start;
    width: 100%;
    background-color: var(--theme-color-alpha-100);
    border-radius: 0.5rem;
    padding: 0.25rem;
  }
}
.header {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
}
.imgBox {
  flex: 1;
  border-radius: 0.25rem;
  overflow: hidden;
  background-color: var(--theme-color-alpha-100);
  min-width: 10rem;
  max-height: 10rem;
  img {
    width: 100%;
  }
}
.infoBox {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.weaponImgBox {
  align-self: flex-end;
  border-radius: 0.25rem;
  overflow: hidden;
  background-color: var(--theme-color-alpha-100);
  width: 5rem;
  max-height: 5rem;
  img {
    width: 100%;
  }
}

.name {
  color: black;
  font-size: 1.5rem;
  padding: 0.5rem;
  border-radius: 0.25rem;
}
[data-element='응결'] {
  .name {
    background-color: var(--element-응결);
  }
}
[data-element='용융'] {
  .name {
    background-color: var(--element-용융);
  }
}
[data-element='전도'] {
  .name {
    background-color: var(--element-전도);
  }
}
[data-element='기류'] {
  .name {
    background-color: var(--element-기류);
  }
}
[data-element='회절'] {
  .name {
    background-color: var(--element-회절);
  }
}
[data-element='인멸'] {
  .name {
    background-color: var(--element-인멸);
  }
}

.levelBox {
  background-color: var(--theme-color-alpha-300);
  border-radius: 0.25rem;
  display: flex;
  gap: 0.5rem;
  span {
    padding: 0.25rem 0.5rem;
    width: 3rem;

    text-align: center;
  }
  select {
    width: 100%;

    padding: 0.25rem 1rem;

    font-size: 1rem;

    color: white;

    border-radius: 0.25rem;
    background-color: var(--theme-color-alpha-100);
  }
}

.skill {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.skillNodes {
  display: flex;
  justify-content: space-between;
}
.skillLine {
  background-color: var(--theme-color-alpha-100);
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  padding: 0.25rem;
  border-radius: 0.5rem;
  gap: 0.25rem;
}
.skillNode {
  background-color: var(--theme-color-alpha-100);
  height: 3rem;
  width: 3rem;
  border-radius: 100%;

  select {
    height: 3rem;
    width: 3rem;
    border-radius: 100%;
    font-size: 1.5rem;
    background-color: var(--theme-color);
    color: black;
  }
}
.skillTags {
  display: flex;
  justify-content: space-between;
  span {
    width: 100%;
    line-height: 1.2;
    font-size: 0.75rem;
  }
}
